
.menu {
    position: fixed;
    width: 10%;
    height: 10%;
    bottom: 0;
    left: 10px;
    z-index: 231;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2em;
    font-weight: bold;
    margin-bottom: 1%;
    cursor: pointer;

    img {
        min-width: 100px;
      width: 60%;
      height: 100%;
      opacity: 50%; 
    }
}

.menu-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: fixed;
    background-color:transparent;
    width: 10%;
    height: 100%;
    z-index: 38;
    left: -200px;

    h3 {
        color: white;
        transform-origin: center;

        a {
            transition: color 1s;
            &:hover {
                cursor: pointer;
                color: rgba($color: white, $alpha: 0.4);
            }
            text-decoration: none;
            color: white;
        }
        
    }

    .menu-underline {
        transform-origin: left;
        margin-bottom: 10vh;
        left: 30%;
        height: 9px;
        background: rgb(223, 230, 236);
        width: 100px;
        transform: rotateY(90deg);
    }

    @keyframes menuBoxLinks {
        0% {
            opacity: 0%;
        }
        0%, 80% {
            transform: rotateY(90deg);
        }
        100% {
            transform: rotateY(0);
        }
    }
}

.menu-content:before {
    content: "";
    display: block;
    height: 1px;
    background: #000;
    
}

.menu-close {
    transform-origin: left;
    width: 5vw;
    height: 5vh;
    z-index: 29;
    clip-path: polygon(20% 0%, 0% 20%, 0 50%, 0% 80%, 20% 100%, 50% 100%, 80% 100%, 100% 80%, 100% 50%, 100% 20%, 80% 0%, 50% 0);
    transform: rotateY(90deg);
    margin-bottom: 12vh;

    img {
        width: 100%;
        height: 100%;
        filter: brightness(90%);
    }

    &:hover {  
        cursor: pointer;
    }
}

.main {
    position: relative;
    width: 100%;
    height: 100vh;
    right: 0;
    background-color: black;

    #chkMenu {
        display: none;
    }

}

.title {

    position: absolute;
    top: 70%;
    right: 10%;
    z-index: 5;

    h1 {
        font-size: 6em;
        color: rgb(201, 205, 223);
    }

}

.contact {
    position: fixed;
    top:101%;
    left: 45%;
    z-index: 120;

    p {
        color: white;
    }
}

