.side-container {
    height: 100%;
    width: 16%;
    position: fixed;
    z-index: 20;
    top: -8.5%;
    right: 0px;
    background-color: #111;
    overflow: scroll;
    transition: 0.5s;
    padding-top: 60px;
    &::-webkit-scrollbar { 
        display: none; 
    } 

    .close-button {
    
        opacity: 0;
        z-index: 250;
        display: none;
        &:before, &:after {
            width: 50px;
            height: 10px;
            z-index: 250;
            transform: rotate(-45deg);
            content: "";
            display: block;
            background-color: white;
            position: fixed;
            top: 25%;
            right: 30%;
            border: 1px solid black;
        }
    
        &:after {
            transform: rotate(45deg);
        }
    
        &:hover {
            cursor: pointer;
        }
    }

    .close {
        font-size: 2em;
        font-weight: bold;
        position: fixed;
        right: 0;
        display: none;
        cursor: pointer;
    }

    .sideChild {
        height: 33.3%;
        cursor: pointer;
        border-bottom: 1px solid #000;
        background: white;
        position: relative;
        z-index: 10;
        
        &:hover h2{
            background: -webkit-linear-gradient(65deg, rgb(140, 184, 221), rgb(155, 128, 229), rgb(252, 217, 255));
            background-clip: text;
            -webkit-text-fill-color: transparent;
        }

        h2 {
            position: absolute;
            top: 40%;
            transition: background 1s;
            background: #000;
            background-clip: text;
            -webkit-text-fill-color: transparent;
            margin-left: 5%;
            z-index: 7;
            width: 100%;
        }

        .content-image {
            width: 100%;
            height: 100%;
            position: absolute; 
            z-index: 5;
            left: 100%;

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                z-index: 5;
            }
        }
    }
}



