.home {
    overflow:hidden;
    width: 84%;
    height: 100%;
    position: relative;

    .border-wrap {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;    
        width: 50%;
        overflow: hidden;
        border-bottom: 3.5px solid;
        border-top: 3.5px solid;
        border-left: 3.5px solid;
        border-image-source: linear-gradient(45deg, rgb(140, 184, 221), rgb(155, 128, 229), rgb(252, 217, 255));
        border-image-slice: 1;
        position: absolute;
        top: 10%;
        right: 0;
        
        .wrap4, .wrap5 {
            position: relative;
            
            h1 {
                font-size: 5em;
                letter-spacing: 15px;
                background: -webkit-linear-gradient(65deg, rgb(140, 184, 221), rgb(155, 128, 229), rgb(252, 217, 255));
                background-clip: text;
                -webkit-text-fill-color: transparent;
            }
            
        }

        .wrap4 {
            margin-right: 25%;
        }
        .wrap5 {
            margin-left: 25%;
        }
    }

    .wrapper {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 20%;
        overflow: hidden;
        position: absolute;
        top: 45%;
        right: 0;

        .wrap1, .wrap2, .wrap3 {
            overflow: hidden;
        }
        

        .middleline {
            width: 2px;
            background-color: white;
            height: 50px;
        }

        p {
            color: white;
            font-size: 2em;
            font-weight: 2px;
        }
    }

    // pour cover up le sidemenu
    .sideMenu2 {
        height: 100%;
        width: 16%;
        position: fixed;
        z-index: 30;
        top: -8.5%;
        right: 0px;
        background-color: rgba($color: #202020, $alpha: 0.8);
        padding-top: 60px;
        display: none;
        opacity: 0; 

        &::after {
            content: '';
            display: block;
            position: absolute;
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
            opacity: 1;
            background: rgba(58, 58, 58, 0.5);
            background-image: radial-gradient(white 2%, transparent 10%);
            background-size: 5px 5px;
            z-index: 10;
        }
    }

    .coverUp {
        position: fixed;
        width: 100vw;
        height: 100vh;
        background-color: rgba($color: #202020, $alpha: 0.8);
        display: none;
        overflow: hidden;
        opacity: 0;

        &::after {
            content: '';
            display: block;
            position: absolute;
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
            opacity: 1;
            background: rgba(58, 58, 58, 0.5);
            background-image: radial-gradient(white 2%, transparent 10%);
            background-size: 5px 5px;
            z-index: 10;
        }
    }

    .content-container {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-around;
        overflow: hidden;
        position: relative;
        background-color: white;
        z-index: 18;
        padding-left: 5%;
        padding-right: 5%;
        opacity: 0;

        

        .contentimage {
            width: 200px;
            height: 250px;
            margin-left: 2.5%;
            
            img {
                width: 200px;
                height: 250px;
            }
        }

        .content-info {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            color: black;
            margin-right: 2.5%;

            .underline2 {
                width: 100%;
                height: 1.5px;
                background: black;
                margin-top: 5%;
                margin-bottom: 5%;
            }
        }
    }
}

